import React, { Component } from 'react'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import { Button } from '@material-ui/core';
import { Link, withRouter } from 'react-router-dom'


 class Navbar extends Component {

    state = {
        isTop: true,
      };
    
      componentDidMount() {
        document.addEventListener('scroll', () => {
          const isScroll = window.scrollY < 100;
          if (isScroll !== this.state.isTop) {
              this.setState({ isTop: isScroll })
          }
        });
      }  

  render() { 
    return (
      <div style={{margin:' 0 auto', display:"flex", justifyContent:'center',}} >
       <AppBar  style={{background:'black', zIndex:"500"}}>
       <Toolbar style={{display:'flex', justifyContent:'space-between', width:'960px',margin:'0 auto'}}>
        <div style={{color:'white'}}>
        <img src='/assets/popcorn.png' alt='' style={{width:'100px', height:'50px'}}/>
        </div>   
        <div style={{display:'flex', }}>

        <Link to='/'>
        <Button variant="outlined" style={{color:"gold", border:"2px solid gold", marginLeft:"10px",
        fontFamily:"'Cinzel Decorative', cursive", fontWeight:"bold"}}>HOME</Button>
        </Link>

        <Link to='/details'>
        <Button variant="outlined" style={{color:"white", border:"2px solid white", marginLeft:"10px",
        fontFamily:"'Cinzel Decorative', cursive", fontWeight:"bold"}}>GAME OF THRONES</Button>
        </Link>

        <Link to='/cast'><Button variant="outlined" style={{color:"white", border:"2px solid white", marginLeft:"10px",
        fontFamily:"'Cinzel Decorative', cursive", fontWeight:"bold"}}>CAST</Button>
        </Link>

        <Link to='/watch'><Button variant="outlined" style={{color:"white", border:"2px solid white", marginLeft:"10px",
        fontFamily:"'Cinzel Decorative', cursive", fontWeight:"bold"}}>WATCH ONLINE</Button>
        </Link>  

        <Link to='/movies'><Button variant="outlined" style={{color:"white", border:"2px solid white", marginLeft:"10px",
        fontFamily:"'Cinzel Decorative', cursive", fontWeight:"bold"}}>MOVIES</Button>
        </Link>  

        </div>
       </Toolbar>   
      </AppBar>   
      </div>
    )
  }
}

export default withRouter(Navbar)